import React, { useState } from "react";
import api from "./services/api";
import logooggi from "./assets/oggi.png";
import { mask as masker, unMask } from "remask";

import "./App.css";

const InputMask = ({ mask, onChange, ...props }) => {
  const handleChange = (ev) => {
    if (mask === undefined) {
      onChange(ev.target.value);
    } else {
      const originalValue = unMask(ev.target.value);
      const maskedValue = masker(originalValue, mask);
      onChange(maskedValue);
    }
  };
  return <input {...props} onChange={handleChange} />;
};

function App() {
  const InitialForm = {
    name: "",
    email: "",
    phone: "",
  };

  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();

  const [errors, setErrors] = useState({});

  function validate() {
    let errors = {};

    if (!name) {
      errors.name = "Informe o seu nome";
    }
    if (!email) {
      errors.email = "Informe um email v√°lido";
    }
    if (!phone) {
      errors.phone = "Informe o telefone com DDD";
    }
    return errors;
  }

  async function handleSubmit(e) {
    e.preventDefault();
    const formulario = {
      name,
      email,
      phone,
    };
    setErrors(validate(formulario));

    await api.post("/leads/3", formulario);
    alert("Dados Cadastrados. Obrigado!");
    clearState();
  }

  function clearState() {
    setName("");
    setEmail("");
    setPhone("");
    setName();
    setEmail();
    setPhone();
  }

  return (
    <>
      <section className="container logo">
        <img src={logooggi} alt="" />
      </section>

      <section className="formulario">
        <div className="content">
          <div className="container formarea">
            <h1>Fique por dentro</h1>
            <p>Preencha os campos abaixo para receber nossos comunicados</p>
            <form onSubmit={handleSubmit}>
              <div>
                <InputMask
                  type="text"
                  name="name"
                  placeholder="Informe o Nome"
                  onChange={setName}
                  value={name}
                />
                {errors.name && <p className="error-input">{errors.name}</p>}
              </div>
              <div>
                <InputMask
                  type="text"
                  name="email"
                  placeholder="Informe o Email"
                  onChange={setEmail}
                  value={email}
                />
                {errors.email && <p className="error-input">{errors.email}</p>}
              </div>
              <div>
                <InputMask
                  type="tel"
                  name="phone"
                  mask={["(99)99999-9999"]}
                  placeholder="Informe o Telefone"
                  onChange={setPhone}
                  value={phone}
                />
                {errors.phone && <p className="error-input">{errors.phone}</p>}
              </div>

              <div className="btn-container">
                <button type="submit" className="btn-cadastrar">
                  Cadastrar
                </button>
              </div>
            </form>
          </div>
        </div>
      </section>
    </>
  );
}

export default App;
